import React from 'react'
import Wrapper from '../subcomponents/wrapper/Wrapper'
import Item1 from '../../images/partners/item1.png'
import Item2 from '../../images/partners/item2.png'
import Item3 from '../../images/partners/item3.png'
import Item4 from '../../images/partners/item4.png'
import Item5 from '../../images/partners/item5.png'
import Item6 from '../../images/partners/item6.png'
import Item7 from '../../images/partners/item7.png'
import Item8 from '../../images/partners/item8.png'
import Item9 from '../../images/partners/item9.png'
import Item10 from '../../images/partners/item10.png'

import './Partners.css'

const Partners = (props) => {
  return (
    <div className="partners">
      <Wrapper medium={true}>
        <div className="partners__title">
          Мы доставляем письма от
        </div>
        <div className="partners__container">
          <div className="partners__item">
            <img src={Item1} alt="item"/>
          </div>
          <div className="partners__item">
            <img src={Item2} alt="item"/>
          </div>
          <div className="partners__item">
            <img src={Item3} alt="item"/>
          </div>
          <div className="partners__item">
            <img src={Item4} alt="item"/>
          </div>
          <div className="partners__item">
            <img src={Item5} alt="item"/>
          </div>
          <div className="partners__item">
            <img src={Item6} alt="item"/>
          </div>
          <div className="partners__item">
            <img src={Item7} alt="item"/>
          </div>
          <div className="partners__item">
            <img src={Item8} alt="item"/>
          </div>
          <div className="partners__item">
            <img src={Item9} alt="item"/>
          </div>
          <div className="partners__item">
            <img src={Item10} alt="item"/>
          </div>
        </div>
      </Wrapper>
    </div>
  )
}

export default Partners;
