import React from 'react'
import ItemImg1 from '../../images/services/servicesItem1.jpg'
import ItemImg2 from '../../images/services/servicesItem2.jpg'
import ItemImg3 from '../../images/services/servicesItem3.jpg'
import ItemImg4 from '../../images/services/servicesItem4.jpg'
import { ReactComponent as Arrow } from '../../svg/arrow.svg'

import "./Services.css"

const Services = (props) => {
  return (
    <div className="services">
      <div className="services__item-container">
        <a href='/' className="services__item services__item_padding-1">
          <img className='services__item-bg' src={ItemImg1} alt="item-bg"/>
          <div className="services__item-title">
            Доставка почтовой коррекспонденции
          </div>
          <div className="services__item-hidden">
            <div className="services__item-sub-title">Простые и заказные письма, бандероли, письма с описью вложений и т.д.</div>
            <Arrow/>
          </div>
        </a>
        <a href='/' className="services__item services__item_padding-2">
          <img className='services__item-bg' src={ItemImg2} alt="item-bg"/>
          <div className="services__item-title services__item-title_width">
            Печать, упаковка и доставка счетов-квитанций
          </div>
          <div className="services__item-hidden">
            <div className="services__item-sub-title">ЖКХ, связь, газо-, электро- и водоснабжения и т.д.</div>
            <Arrow/>
          </div>
        </a>
        <a href='/' className="services__item services__item_padding-1">
          <img className='services__item-bg' src={ItemImg3} alt="item-bg"/>
          <div className="services__item-title">
            Экспресс-доставка по России и миру
          </div>
          <div className="services__item-hidden">
            <div className="services__item-sub-title">Срочная доставка документов и грузов по г. Ульяновску, России и миру.</div>
            <Arrow/>
          </div>
        </a>
        <a href='/' className="services__item services__item_padding-3">
          <img className='services__item-bg' src={ItemImg4} alt="item-bg"/>
          <div className="services__item-title">
            Рекламные услуги
          </div>
          <div className="services__item-hidden">
            <div className="services__item-sub-title">Доставка рекламных материалов по почтовым ящикам и фирмам города.</div>
            <Arrow/>
          </div>
        </a>
      </div>
    </div>
  )
}

export default Services;