import React from 'react'

import './Wrapper.css'

const Wrapper = (props) => {
  const {
    children,
    className,
    medium
  } = props;

  return (
    <div className={'wrapper ' + (className ? className : '') + (medium ? ' wrapper_medium' : '')}>
      {children}
    </div>
  )
}

export default Wrapper;
