import React from 'react'
import { Link } from 'react-router-dom'

import Wrapper from '../subcomponents/wrapper/Wrapper'
import TraceForm from '../subcomponents/trace-form/TraceForm'
import Logo from '../../images/logo.png'
import { ReactComponent as LogoLc } from '../../svg/lc.svg'

import './NavigationBar.css'

const NavigationBar = (props) => {
  return (
    <div className='navigation'>
      <Wrapper className='navigation__container'>
        <div className='navigation__container-flex'>
          <a href="/"><img src={Logo} alt="logo" className='navigation__logo'/></a>
          <TraceForm />
        </div>
        <div className='navigation__container-flex'>
          <div className='navigation__links'>
            <a href='/services'>Услуги</a>
            <a href='/vacancies'>Вакансии</a>
            <a href='/contacts'>Контакты</a>
          </div>
          <a href='/lc' className='navigation__lc'>
            <LogoLc/>
            Личный кабинет
          </a>
        </div>
      </Wrapper>
    </div>
  )
}

export default NavigationBar;
